
import XButton from 'vux/src/components/x-button/index.vue'

import api from "../api.js";
import axios from "axios";
export default {
components: {
XButton
},
props: {
id: {
type:Number,

default: 0
}
},

data() {
return {
value: "",
act_id:0,
act: {
id: 1,
type: "",
name: "",
valid_date: "2019-09-09",
apply_way: "",
school: "",
taglist: ",,",
url: "http://www.scut.edu.cn",
litimg_url: "http://www.scut.edu.cn",
pic_url: "http://www.scut.edu.cn",
location: "",
act_detail: "ActsBoard400..."
}
};
},
methods: {
onChange() {

},
get_activity_by_id(id) {
var _this = this;
axios
.get(api.get_activity_by_id(id))
.then(function(response) {
console.log(response.data);



_this.act = response.data.data;

})
.catch(function(error) {
console.log(error);
});
}
},
mounted: function() {


console.log("id" + this.$route.params.act_id);
this.act_id = this.$route.params.act_id;
this.get_activity_by_id(this.act_id);
}
};



