import api from "./api"
import axios from 'axios'
// import wx from 'weixin-js-sdk'
export default {
  getCode: (state) => {
    const AppId = 'wxb569d7a3f448c503';
    // console.log(this.a);
    // const code = getUrlParam("code"); //getUrlParam
    let code;
    var reg = new RegExp("(^|&)code=([^&]*)(&|$)"); //  
    var r = window.location.search.substr(1).match(reg); //  
    if (r != null)
      code = unescape(r[2]);
    const local = window.location.href; //url
    console.log('Local' + local);
    const redirect_uri = "http://web.iamxuyuan.com/";
    //openid
    console.log('code=' +code);
    let if_open_id = document.cookie.indexOf("openid");
    console.log('openid' + if_open_id);
    // openid
    axios.post('http://web.iamxuyuan.com/s11111111111/' + this.code, {
      code: this.code //~
    });
    if (if_open_id < 0) {
      if (code == null || code === "") {
        // axios.post(api.post_code, {
        //     code: this.code //~
        //   })
        //   .then(function (response) {
        //     console.log(response);
        //     console.log('post code')
        //   })
        //   .catch(function (error) {
        //     console.log(error);
        //   });
        console.log('-')
        window.location.href = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=" + AppId + "&redirect_uri=http://web.iamxuyuan.com&response_type=code&scope=snsapi_base&state="+state+"#wechat_redirect"; //,url
      } else {
        //openidurlcode
        // code 
        console.log('')
        axios.post(api.post_code, {
            code: code //~
          })
          .then(function (response) {
            console.log(response);
            console.log('post code')
          })
          .catch(function (error) {
            console.log(error);
          });
        console.log('-')
      }
    }
  },
  //url
  getUrlParam: (name) => {
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //  
    var r = window.location.search.substr(1).match(reg); //  
    if (r != null) return unescape(r[2]);
    return null; //  
  },

  //
  to_share() {

  }
}



