import Select from "./select";
import activityOutline from "./activityOutline";
import axios from "axios";
import api from "../api";
// import tab from './tab'
export default {
  components: {
    Select,
    activityOutline
    // tab
  },
  name: "tab",
  data() {
    return {
      to_id: 0,
      activities: [
        {
          // id: 1,
          // type: "", // 
          // name: "ActsBoard", // 
          // valid_date: "2019-09-09", // 23:59
          // school: "", // 
          // taglist: ",,", // 
          // litimg_url: "http://www.scut.edu.cn", // 
          // pic_url: "http://www.scut.edu.cn", // 
          // location: "", // 
          // act_detail: "ActsBoard400..." // 
        }
      ],
      time: "",
      type: ""
    };
  },
  methods: {
    get_activities() {
      // console.log(api.get_acts);
      var _this = this;
      axios
        .get(api.get_acts)
        .then(function(response) {
          console.log(response.data);

          // _this.activities = JSON.parse(response.data).data;
          _this.activities = response.data.data;
          // console.log(_this.activities);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    get_activities_by_type(time, type) {
      console.log("" + time + type);
      var _this = this;
      axios
        .get(api.get_activity_after_select(time, type))
        .then(function(response) {
          console.log(response.data);

          // _this.activities = JSON.parse(response.data).data;
          _this.activities = response.data.data;
          // console.log(_this.activities);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    to_act_detail(id) {
      console.log("id" + id);
      // this.to_id = id;
      this.$router.push({
        name: "activityDetail",
        params: {
          act_id: id
        }
      });
    }
  },
  mounted: function() {
    this.get_activities();
  },
  created() {
    let state;
    var reg = new RegExp("(^|&)state=([^&]*)(&|$)"); //
    var r = window.location.search.substr(1).match(reg); //
    if (r != null) {
      state = unescape(r[2]);
    }
    if (state) {
      console.log('')
      //this.$router.push({ path: "/" + state });
    }
  }
};



